.Header {
    display: inline-flex;
    width: 100%;
    height: 140px;
    background-image: radial-gradient(circle, #fcfdfd, #f9f9f9, #f5f5f6, #f2f2f2, #eeeeee);
    justify-content: space-around;
    align-items: center;
}
.Menu {
    list-style-type: none;
    display: flex;
    overflow: hidden;
    font-weight: 500;
    font-family: 'Roboto' sans-serif;
    font-size: larger;
}
li {
    float: left;
    padding: 20px;
}
a {
    text-decoration: none;
    color: rgb(56, 56, 56);
}
.Logo:hover {
    cursor: pointer;
}

@media (max-width: 768px) {
    .Header {
        display: inline-flex;
        width: 100%;;
        height: 200px;
        background-image: radial-gradient(circle, #fcfdfd, #f9f9f9, #f5f5f6, #f2f2f2, #eeeeee);
        justify-content: end;
        align-items: center;
    }   
    li {
        float: left;
        padding: 10px;
    }
    .Logo {
        width:140px;
        height:50px;
        float:left;
        padding-left: 300px;
    }
    ul {
        padding-left: 20px;
    }
}